#header {
  background-color: #f60;
  text-align: center;
  height: 1.333333333333333rem;
  position: relative;
}
#header h1 {
  margin: 0;
  color: #fff;
  line-height: 1.333333333333333rem;
  font-size: 0.48rem;
}
#header .top-icon-left {
  position: absolute;
  width: 1.333333333333333rem;
  height: 100%;
}
#header .top-icon-left:before {
  display: block;
  content: '';
  position: absolute;
  width: 0.266666666666667rem;
  height: 0.266666666666667rem;
  border-top: 3px solid #fff;
  border-left: 3px solid #fff;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%) rotate(-45deg);
          transform: translate(-50%, -50%) rotate(-45deg);
}


