.order-link {
  border-bottom: 0;
  background-repeat: repeat-x;
  background-size: 100% 1px;
  background-position: left bottom;
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(0.5, rgba(0,0,0,0)), color-stop(0.5, #ddd), to(#ddd));
  background-color: #fff;
}
.order-link .flex {
  text-align: center;
  padding: 0.4rem 0;
  font-size: 12px;
  color: #757272;
}
[data-dpr="2"] .order-link .flex {
  font-size: 24px;
}
[data-dpr="3"] .order-link .flex {
  font-size: 36px;
}
.order-link .flex i.iconfont {
  display: block;
  line-height: 1;
}
.order-link .flex:last-child {
  border-left: 0;
  background-position: left top;
  background-image: -webkit-gradient(linear, right top, left top, color-stop(0.5, rgba(0,0,0,0)), color-stop(0.5, #ddd), to(#ddd));
  background-repeat: repeat-y;
  background-size: 1px 100%;
}
.section {
  background-color: #fff;
  margin: 0.266666666666667rem 0;
  border-top: 0;
  border-bottom: 0;
  background-repeat: repeat-x;
  background-size: 100% 1px;
  background-image: -webkit-gradient(linear, left bottom, left top, color-stop(0.5, rgba(0,0,0,0)), color-stop(0.5, #ddd), to(#ddd)), -webkit-gradient(linear, left top, left bottom, color-stop(0.5, rgba(0,0,0,0)), color-stop(0.5, #ddd), to(#ddd));
  background-position: top, bottom;
}
ul.ui-list.userlist i {
  margin-right: 0.266666666666667rem;
}
ul#user-one li:first-child i {
  color: #9d34bf;
}
ul#user-one li:nth-child(2) i {
  color: #41bd21;
}
ul#user-one li:nth-child(3) i {
  color: #ea8631;
}
ul#user-one li:nth-child(4) i {
  color: #3195ea;
}
ul#user-one li:nth-child(5) i {
  color: #423d21;
}
ul#user-two li:first-child i {
  color: #d82a2a;
}
ul#user-two li:nth-child(2) i {
  color: #009688;
}


