.styled {
  margin: 0;
  padding: 0;
  list-style: none;
}
ul.ui-list {
  padding-left: 0.266666666666667rem;
}
ul.ui-list li {
  padding: 10px 0;
  position: relative;
}
ul.ui-list li:last-child {
  background-size: 100% 0;
}
ul.ui-list li.arrow-right:after {
  content: "";
  display: block;
  height: 0.213333333333333rem;
  width: 0.213333333333333rem;
  border-width: 2px 2px 0 0;
  border-color: #ddd;
  border-style: solid;
  position: absolute;
  right: 0.266666666666667rem;
  top: 50%;
  -webkit-transform: translateY(-50%) rotate(45deg);
  transform: translateY(-50%) rotate(45deg);
}
.banner {
  height: 4.666666666666667rem;
  position: relative;
}
.banner img {
