}
.banner .banner-title {
  margin: 0;
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  color: #ddd;
  background: -webkit-linear-gradient(transparent, rgba(0,0,0,0.2));
  background: linear-gradient(transparent, rgba(0,0,0,0.2));
